/* ----------------------------------------------------------------
	Add here styles for widgets. Thanks!
-----------------------------------------------------------------*/


.widget {
	position: relative;

	.widget-title {
		color: #faf7fa;
		font-size: 0.875rem;
		margin-bottom: $spacer * 1.375;
	}

	// Widget - Social
	&.widget-social {
		.social-icons {
			padding-top: $spacer * 0.75;

			li {
				padding: 0 $spacer * 0.25;

				&:last-child {
					padding-right: 0;
				}

				a {
					background-color: #303135;
					color: $white;
					display: inline-block;
					line-height: 1.8125rem;
					width: 1.8125rem;
					height: 1.8125rem;
					vertical-align: middle;
					text-align: center;
					@include border-radius(50%);
					@include transition(all 0.3s ease-in-out);

					&:hover {
						background-color: $theme-color;
						color: $white;
					}

					i {
						vertical-align: top;
						line-height: 1.8125rem;
					}
				}
			}
		}
	}

	// Widget - Links
	&.widget-links {
		ul {
			width: 50%;

			li {
				a {
					color: #61605f;
					font-size: 1rem;
					line-height: 1.875rem;

					&:hover {
						color: $theme-color;
					}
				}
			}
		}
	}

	// Widget - Payments
	&.widget-payment {
		.payments {
			display: flex;

			.payment-icon {
				display: inline-block;
				vertical-align: middle;
				margin-right: $spacer * 0.25;
				width: 3.5rem;
				height: 2rem;
				background-color: #d6d3cc;
				background-size: 80% auto;
				background-repeat: no-repeat;
				background-position: center;

				@include border-radius(0.25rem);

				&.visa {
					background-image: url(../images/payments/visa.svg);
				}

				&.paypal {
					background-image: url(../images/payments/paypal.svg);
				}

				&.express {
					background-image: url(../images/payments/express.svg);
				}

				&.mastercard {
					background-image: url(../images/payments/mastercard.svg);
				}
			}
		}
	}

	// Widget - custom html
	&.widget-custom-html {
		.widget-content {
			p, a {
				color: #61605f;
				display: block;

				&.h4 {
					font-size: 1.5rem;
					line-height: 1.25;
				}
			}
		}
	}

	// Widget - Subscribe
	&.widget-subscribe {
		.widget-content {
			#widget-subscribe-form {
				.form-control,
				.btn {
					@include border-radius(1.625rem);
				}

				.input-group-append {
					margin-left: -$spacer * 2.875;

					.btn {
						background-color: $yellow;
						border-color: $yellow;
					}
				}
			}
		}
	}

	// Widget - Products
	&.widget-products {
		.widget-content {
			.product {
				display: flex;
				align-items: center;
				margin-bottom: $spacer * 0.625;

				.product-image {
					flex: 1;
					height: 8.25rem;
					max-width: 9.75rem;
				}

				.product-details {
					flex: 1;
					padding: 0 $spacer * 1.25;
				}

				.product-price {
					margin-bottom: $spacer * 0.625;
				}

				.product-title {
					a {
						color: $white;
					}
				}
			}
		}
	}

	// Widget - Latest Posts
	&.widget-latest-posts {
		.widget-content {
			.post {
				margin-bottom: 1.875rem;

				.post-thumbnail {
					padding-right: 1.875rem;
				}

				.entry-title {
					a {
						font-size: 1.1875rem;
						line-height: 1.875rem;
					}
				}
			}
		}
	}
}