/* ----------------------------------------------------------------
	Add here styles for variables. Thanks!
-----------------------------------------------------------------*/


//-------------------- Base Colors --------------------//

// d6c6ef
$theme-color:								#8d76b2;
$body-color:								#302f2d;
$headings-color:							#000000;
$yellow:									#ffc83a;
$red:										#ea0909;
$blue:										#5394cf;

//-------------------- Font Families --------------------//

$body-font:									'Open Sans', sans-serif;
$headings-font:								'Roboto Slab', serif;;


//-------------------- Redefine Bootstrap Variables ------------//

// Theme Colors
$primary:									$theme-color;
$secondary:									$yellow;

// Gutter Width
$grid-gutter-width:							4rem;

// Link
$link-color:								$headings-color;
$link-decoration:							none;
$link-hover-color:							#ddcef5;

// Border
$border-radius:								0.5rem;

// Font sizes
$font-size-base:							1rem;

$h1-font-size:								7.5rem;
$h2-font-size:								6.125rem;
$h3-font-size:								2.5rem;
$h4-font-size:								1.75rem;
$h5-font-size:								1.3125rem;
$h6-font-size:								1.1875rem;

// Line Height
$line-height-base:							30 / 16;
$line-height-sm:							30 / 19;
$line-height-lg:							30 / 14;

// Font Weight
$font-weight-base:							300;

// Forms
$input-color:								$body-color;
$input-border-color:						$theme-color;
$input-border-width:						0;
$input-btn-focus-color-opacity: 			1;

$input-btn-padding-y:			 			0.875rem;
$input-btn-padding-x:						1.25rem;
$input-btn-font-size:						0.875rem;

$input-btn-padding-y-sm:					0.67rem;
$input-btn-padding-x-sm:					0.83rem;
$input-btn-font-size-sm:					0.583rem;

$input-btn-padding-y-lg:					1.33rem;
$input-btn-padding-x-lg:					1.67rem;
$input-btn-font-size-lg:					1.17rem;

$input-btn-line-height:			 			1;

$input-btn-focus-width:						1px;
$input-focus-border-color:					$theme-color;

$input-border-radius:						1.375rem;
$input-border-radius-sm:					0.916rem;
$input-border-radius-lg:					1.83rem;

$input-font-weight:							300;
$input-focus-box-shadow:					0 0 0 1px #e5e8e8;

// Buttons
$btn-font-family:				 			$headings-font;

$btn-padding-y:					 			1.03125rem;
$btn-padding-x:					 			3.25rem;
$btn-font-size:								0.75rem;

$btn-padding-y-sm:							0.75rem;
$btn-padding-x-sm:							2.67rem;
$btn-font-size-sm:							0.583rem;

$btn-padding-y-lg:							1.5rem;
$btn-padding-x-lg:							5.33rem;
$btn-font-size-lg:							1.17rem;

$btn-font-weight:				 			800;
$btn-line-height:							1;

$btn-border-width:							1px;

$btn-border-radius:				 			1.375rem;
$btn-border-radius-sm:						0.916rem;
$btn-border-radius-lg:						1.83rem;

// Navbar
$navbar-padding-y:							2.5rem;
$navbar-brand-padding-y:					0;
$navbar-nav-link-padding-x:			 		1.5rem;
$nav-link-height:							1;

$navbar-light-color:						$body-color;
$navbar-light-hover-color:					$body-color;
$navbar-light-active-color:					$body-color;
$navbar-light-toggler-icon-bg:				url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 30 30'><path stroke='#{rgba($theme-color, .75)}' stroke-linecap='round' stroke-miterlimit='10' stroke-width='2' d='M4 7h22M4 15h22M4 23h22'/></svg>");
$navbar-light-toggler-border-color:			$body-color;

$navbar-dark-color:							#ffffff;
$navbar-dark-hover-color:					#ffffff;
$navbar-dark-active-color:		 			#ffffff;
$navbar-dark-toggler-icon-bg:				url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 30 30'><path stroke='#{rgba($theme-color, .75)}' stroke-linecap='round' stroke-miterlimit='10' stroke-width='2' d='M4 7h22M4 15h22M4 23h22'/></svg>");
$navbar-dark-toggler-border-color:			#ffffff;

$navbar-toggler-border-radius:				0;

// Navs
$nav-link-padding-y:						1rem;
$nav-link-padding-x:						1.5rem;
$nav-link-font-size:						0.9375rem;
$nav-link-font-weight:						400;
$nav-link-color:							$body-color;
$nav-link-hover-color:						$body-color;

// Tabs
$nav-tabs-border-color:						$theme-color;
$nav-tabs-border-width:						2px;
$nav-tabs-border-radius:					0;
$nav-tabs-link-hover-border-color:			$theme-color;
$nav-tabs-link-active-color:				$theme-color;
$nav-tabs-link-active-border-color:		 	$theme-color;


// Accordion
$accordion-padding-y:					 	1.875rem;
$accordion-padding-x:					 	2.25rem;
$accordion-color:						 	$theme-color;
$accordion-bg:								rgba($theme-color, 0.3);
$accordion-border-width:					0;
$accordion-border-radius:				 	0;

$accordion-button-color:					$body-color;
$accordion-button-bg:						transparent;
$accordion-button-active-bg:				transparent;
$accordion-button-active-color:				$theme-color;

$accordion-button-focus-border-color:		none;
$accordion-button-focus-box-shadow:		 	none;

$accordion-icon-width:						1.25rem;
$accordion-icon-color:						$body-color;


// Pagination
$pagination-bg:								transparent;
$pagination-padding-y:					 	1rem;
$pagination-padding-x:						1rem;
$pagination-border-radius:					0.5rem;
$pagination-border-width:				 	0;

$pagination-hover-color:					#FFFFFF;
$pagination-hover-bg:						$theme-color;

$pagination-active-color:				 	#FFFFFF;
$pagination-active-bg:						$theme-color;


// Options
$enable-shadows:							true;


//-------------------- Top Bar --------------------//

$topbar-color:								#ddcef5;
$topbar-hover-color:						#ddcef5;


//-------------------- Header --------------------//

$header-height:					 			9.25rem;

// Sticky Header
$sticky-header-height:						5.625rem;


//-------------------- Primary Menu --------------------//

$primary-menu-padding:						2.75rem;
$sticky-menu-padding:						2.0625rem;

$primary-menu-font:							$body-font;
$primary-menu-font-weight:					400;
$primary-menu-font-size:					0.9375rem;
$primary-menu-font-tt:						initial;
$primary-menu-font-color:					#FFFFFF;
$primary-menu-font-spacing:					0;

// top Button
$top-btn-font-size:							1.25rem;
$top-btn-font-color:						#FFFFFF;


//-------------------- Section --------------------//

$section-spacer:							5rem;


//-------------------- SVG Icons --------------------//

