$external-link-icon: url("data:image/svg+xml,<svg width='18' height='18' viewBox='0 0 18 18' fill='none' xmlns='http://www.w3.org/2000/svg'><path d='M12.2424 5.75734L5.17136 12.8284' stroke='#{$theme-color}' stroke-width='2' stroke-miterlimit='10' stroke-linecap='square' stroke-linejoin='round'/><path d='M4.81791 4.69668H13.3032V13.182' stroke='#{$theme-color}' stroke-width='2' stroke-miterlimit='10' stroke-linecap='square'/></svg>");
$checker-icon: url("data:image/svg+xml,<svg width='34' height='28' viewBox='0 0 34 28' fill='none' xmlns='http://www.w3.org/2000/svg'><path d='M32.9175 0.669397C32.1147 0.0960544 31.0826 0.210723 30.5092 1.0134L13.6507 22.9151L3.78784 14.4296C3.09974 13.8563 1.95289 13.8563 1.37947 14.5443C0.806054 15.2323 0.806061 16.379 1.49416 16.9523L12.7332 26.5845C13.0772 26.8138 13.4213 27.0432 13.88 27.0432C13.9947 27.0432 13.9947 27.0432 14.1094 27.0432C14.5681 27.0432 15.0269 26.6992 15.2562 26.3552L33.1469 2.96277C33.835 2.38943 33.7203 1.24274 32.9175 0.669397Z' fill='#{$theme-color}'/></svg>");
$arrow-up-icon: url("data:image/svg+xml,<svg width='21' height='12' viewBox='0 0 21 12' fill='none' xmlns='http://www.w3.org/2000/svg'><path d='M19.3999 10.5L10.3999 1.5L1.3999 10.5' stroke='#{$theme-color}' stroke-width='1.5' stroke-miterlimit='10' stroke-linecap='round' stroke-linejoin='round'/></svg>");
$arrow-down-icon: url("data:image/svg+xml,<svg width='21' height='12' viewBox='0 0 21 12' fill='none' xmlns='http://www.w3.org/2000/svg'><path d='M19.3999 1.5L10.3999 10.5L1.3999 1.5' stroke='#{$theme-color}' stroke-width='1.5' stroke-miterlimit='10' stroke-linecap='round' stroke-linejoin='round'/></svg>");